import React, { ReactElement } from 'react';

// my-app/src/common/utils/textAnimations.tsx

export const splitTitleIntoWords = (title: string) => {
  return title.split(' ').map((word, index) => (
    <React.Fragment key={index}>
      <span className={`word word-${index}`}>{word}</span>{' '}
    </React.Fragment>
  ));
};

// Function to handle paragraph splitting into letters and words with preserved spaces
export const wrapParagraphs = (text: string): ReactElement[] => {
  let wordIndex = 0;
  let elements: ReactElement[] = [];
  // Split the text by spaces to get words and potential line breaks
  const words = text.split(' ');

  words.forEach((word, wordIdx) => {
    // Split by line breaks within the word to handle them separately
    const parts = word.split('\n');
    parts.forEach((part, partIdx) => {
      // For each part that's not the first, insert a line break
      if (partIdx > 0) {
        elements.push(<br key={`br-${wordIndex}`} />);
        wordIndex++;
      }
      // Add the non-empty parts as spans
      if (part) {
        elements.push(
          <span key={`part-${wordIndex}`} className={`letter letter-${wordIndex}`}>
            {part}
          </span>
        );
        wordIndex++;
      }
    });
    // After each word (and its parts), add a space unless it's the last word
    if (wordIdx < words.length - 1) {
      elements.push(
        <span key={`space-${wordIndex}`} className="letter">
          &nbsp; {/* Use a non-breaking space to ensure it's rendered */}
        </span>
      );
      wordIndex++;
    }
  });

  return elements;
};
